import AboutMeSection from '../components/AboutMeSection';
import ContactSection from '../components/ContactSection';
import EducationSection from '../components/EducationSection';
import ProjectSection from '../components/ProjectSection';
import SkillSection from '../components/SkillSection';

export let sections = [
	{
		id: 'aboutMe',
		isAlternate: true,
		variant: 0,
		sectionComponent: () => (
			<AboutMeSection
				title="About Me"
				isAlternate={true}
			/>
		),
	},
	{
		id: 'education',
		variant: 1,
		sectionComponent: () => <EducationSection title="Education" />,
	},
	{
		id: 'skills',
		isAlternate: true,
		variant: 3,
		sectionComponent: () => (
			<SkillSection
				title="Skills"
				isAlternate={true}
			/>
		),
	},
	{
		id: 'projects',
		variant: 2,
		sectionComponent: () => <ProjectSection title="Projects" />,
	},
	{
		id: 'contactMe',
		isAlternate: true,
		variant: 0,
		sectionComponent: () => (
			<ContactSection
				title="Get in touch"
				isAlternate={true}
				isSmaller={true}
			/>
		),
	},
];
